
<template>
    <Modal ref="ModalRef">
        <template v-slot:header>
            <div>
                <h2 class="bold">
                    <span>Swap Schedule</span>
                    
                </h2>
                <p class="mt-2">This dialog allows you to swap the schedules of two employees.</p>
            </div>
        </template>
        <template v-slot:body>
            
            <div class="flex swap-body mt-4">
                <div class="swap-sche-body edit-form ">
                    <h3>Schedule 1</h3> <div></div>
                    <label>Date</label>
                    <DatePickerInput :initSourceData="sourceData.date"></DatePickerInput>    
                    <label>Employee</label>
                    <label>{{ sourceData.employeeName }}</label>
                    <label>Employee No.</label>
                    <label>{{  sourceData.employeeNo }}</label>

                    <label>Position</label>
                    <span>{{ sourceData.scheduleDetail.positionTitle }}</span>
                    <label>Start Time</label>
                    <span>{{ sourceData.startTime }}</span>
                    <label>Finish Time</label>
                    <span>{{ sourceData.endTime }}</span>
                    <label>Unpaid Break</label>
                    <span>{{ sourceData.scheduleDetail.breakLength }}</span>
                    <label>Net Hours</label>
                    <span>{{ sourceData.hours }}</span>
                </div>
                <div class="swap-sche-body edit-form">
                    <h3>Schedule 2</h3> <div></div>
                    <label>Date</label> 
                    <DatePickerInput :initSourceData="sch2Data.date"
                        @updateDate="getSche2Data">
                    </DatePickerInput>    
                    <label>Employee</label>
                    <custom-select
                        :options="employeeList"
                        :displayKey="'commonName'"
                        :selected="sch2Data.employee"
                        @selectOption="selectEmployee2">
                    </custom-select>
                    <label>Employee No.</label>
                    <label>{{  sch2Data.employeeNo }}</label>
                    <template v-if="sch2Data && sch2Data.employee && sch2Data.date">
                        <label>Position</label>
                        <span>{{ sch2Data.scheduleDetail?.positionTitle }}</span>
                        <label>Start Time</label>
                        <span>{{ sch2Data.startTime }}</span>
                        <label>Finish Time</label>
                        <span>{{ sch2Data.endTime }}</span>
                        <label>Unpaid Break</label>
                        <span>{{ sch2Data.scheduleDetail?.breakLength }}</span>
                        <label>Net Hours</label>
                        <span>{{ sch2Data.hours }}</span>
                    </template>
                    
                </div>
            </div>
        </template>
        <template v-slot:footer>
            <div class="mt-4 full-width">
                <div class="d-flex justify-end">
                    <button class="white normal copy2">
                        Swap
                    </button>
                    <button class="orange normal copy2 ml-4" @click="close()">
                        Cancel
                    </button>
                </div>
            </div>
        </template>
    </Modal>
</template>

<script lang="ts">

import { defineComponent, nextTick, ref } from "vue";
import Modal from "@/components/common/Modal.vue";
import CustomSelect from "@/components/common/CustomSelect.vue";
import moment from "moment";
import DatePickerInput from "@/components/common/DatePickerInput.vue";
import { ScheduleDay } from "./FlexScheduleInterface";
import _ from "lodash";
import { useStore } from 'vuex';
import { EmployeeGetters, ManagedEmployee } from "@/store/employeeModule";
import Remote from "@/remote/scheduleRequest";
import { keysToLowerCase } from "../common/script";

type ScheduleDaySwap = ScheduleDay & {
    employee: ManagedEmployee;
    employeeName: string;
    employeeNo: number;
}

export default defineComponent({
    components: {
        Modal,
        CustomSelect,
        DatePickerInput
    },
    setup() {
        const store = useStore(); 
        const employeeList = ref([]);
        const sourceData = ref<ScheduleDaySwap>({} as ScheduleDaySwap);
        const sch2Data = ref<ScheduleDaySwap>({} as ScheduleDaySwap);

        // create const modalRef
        const ModalRef = ref<InstanceType<typeof Modal>>();

        sch2Data.value.date = moment().format();
        console.log('employeeList', employeeList.value)

        function selectEmployee2(employee: any) {
            console.log('selectEmployee2', employee)
            sch2Data.value.employee = employee;
            sch2Data.value.employeeName = employee.employeeName;
            sch2Data.value.employeeNo = employee.employeeNo;
            nextTick(getSche2Data);
        }

        function getSch1Data() {
            if (!validateDateAndEmployee(sch2Data.value)) {
                return 
            }

            // call backend to get sch2Data
        }
        function getSche2Data() {
            if (!validateDateAndEmployee(sch2Data.value)) {
                return 
            }
            console.log('sch2Data', sch2Data.value)
            const requestDate = moment(sch2Data.value.date).format('YYYY-MM-DD');
            const param = {
                employeeNo: sch2Data.value.employeeNo,
                dateFrom: requestDate, // dateFrom: '2020-01-01',
                dateTo: requestDate, // dateTo: '2021-01-01',
                forManagedEmployees: false,
            };

            Remote.GetFlexSchedules(param)
            .then(res => {
                console.log('res', res)
                const employeeSche = res.FlexSchedules[0]
                sch2Data.value.scheduleDetail = keysToLowerCase(employeeSche.ScheduleData);
                sch2Data.value.startTime =  employeeSche.StartTime ? moment(employeeSche.StartTime).format('HH:mm') : '';
                sch2Data.value.endTime = employeeSche.EndTime ? moment(employeeSche.EndTime).format('HH:mm') : '';
                sch2Data.value.hours = employeeSche.Hours;
            })
            // call backend to get sch2Data
        }

        function validateDateAndEmployee(targetData: ScheduleDaySwap) {
            if (targetData.date === '' || targetData.employee === null) {
                return false;
            }
            return true;
        }

        function open() {
            ModalRef.value!.toggleShow(true);
            sourceData.value = _.clone(store.state.FlexSchedule.selectedFlexDay);
            employeeList.value = store.getters[EmployeeGetters.getEmployees];
            console.log('flex day', sourceData.value)
            
        }
        function close() {
            ModalRef.value!.toggleShow(false);
        }
       
        return {
            employeeList,
            sourceData,
            sch2Data,
            ModalRef,
            selectEmployee2,
            open,
            close,
            getSche2Data
        }
    },

    // data() {
    //     return {
    //         sourceData: <any>{},
    //         targetData: <any>{},
    //         sch1: {
    //             date: '22/03/2023',
    //             employeeName: 'Joe',
    //             employeeNo: '1234',
    //             position: 'Manager',
    //             startTime: '9:00 AM',
    //             endTime: '5:00 PM',
    //             break: '1:00 PM - 2:00 PM',
    //             hours: 8
    //         },
    //         sch2: {
    //             date: moment(),
    //             employeeName: '',
    //             employeeNo: null,
    //             position: '',
    //             startTime: '',
    //             endTime: '',
    //             break: '',
    //             hours: null
    //         },
    //         dateOptions: [
    //             '22/03/2023',
    //             '23/03/2023',
    //             '24/03/2023'
    //         ],
    //         employeeOption: [
    //             'Joe',
    //             'Robert',
    //             'John',
    //             'Jack',
    //             'Jill'
    //         ]
    //     }
    // },

    mounted() {
        // (this.$refs.ModalRef as any).toggleShow();
    },

    methods: {
        // selectDate1(date: string) {
        //     this.sch1.date = date;
        // },
        // selectDate2(date: string) {
        //     this.sch2.date = date;
        // },
        // selectEmployee1(employee: string) {
        //     this.sch1.employeeName = employee;
        // },
        // selectEmployee2(employee: string) {
        //     this.sch2.employeeName = employee;
        // },

        // open() {
        //     (this.$refs.ModalRef as any).toggleShow(true);
        //     this.sourceData = _.clone(this.$store.state.FlexSchedule.selectedFlexDay);
            
        //     console.log('flex day', this.sourceData)
            
        // },
        // close() {
        //     (this.$refs.ModalRef as any).toggleShow(false);
        // },
       
    }
})
</script>

<style scoped lang="scss">
@use "../../style/colors";
@use "../../style/layouts";
.swap-body {
    display: flex;
    min-width: 700px;
}

.swap-sche-body {
    width: 50%;
}

.edit-form {
    display: grid;
    grid-template-columns: 140px 150px;
    grid-template-rows: repeat(9, min-content);
    row-gap: 8px;
    align-items: center;

    input {
        height: 30px;
        border-radius: 5px;
        border: 1px solid colors.$grey;
        padding: 0 10px;

        &:focus {
            outline: none;
        }
    }

    @media screen and (max-width: layouts.$xs-screen) {
        font-size: 12px;
        grid-template-columns: 120px 120px;
    }
}
</style>